import ttNavBar from '@/components/tt-nav-bar/tt-nav-bar.vue'
import ttPicker from '@/components/tt-picker/tt-picker.vue'
import ttLabels from '@/components/tt-labels/tt-labels.vue'
import Photo from '../../components/photo/photo.vue'
export default {
	components:{ttNavBar,ttPicker,ttLabels,Photo},
	data() {
		let statusBarHeight = uni.getSystemInfoSync().statusBarHeight +'px'
		let info = this.$store.state.user.userInfo
		return {
			statusBarHeight: statusBarHeight,
			checkBoxHeight: 0,
			backgroundColor:'#fff',
			title: '编辑个人资料',
			color: '#000',
			addr:'',
			info: info,
			texarea: true,
		}
	},
	created(){
		this.init()
	},
	methods: {
		init(){
			wx.getSetting({
			  success(res) {
			    if (!res.authSetting['scope.writePhotosAlbum']) {
			      wx.authorize({
			        scope: 'scope.writePhotosAlbum',
			        success () {
			          // 用户已经同意小程序使用录音功能，后续调用 wx.startRecord 接口不会弹窗询问
			          // wx.startRecord()
			        }
			      })
			    }
			  }
			})
		},
		map(){
			let _this = this
			new Promise((resolve,reject)=>{
				uni.chooseLocation({
				    success: function (res) {
							resolve(res)
				    }
				})
			}).then((res)=>{
				_this.info.addr = _this.addr = res.address
				_this.info.lat = res.latitude
				_this.info.lon = res.longitude
			})
			
		},
		back(){
			uni.navigateBack()
		},
		changeValue(data){
			this.info.experienceTime = data
		},
		chTextarea(data){
			this.texarea = data
		},
		getPhoto(data){
			let _this = this
			let url




			url = `${this.GLOBAL.baseURL}/qiniu/config`

			data.forEach(e=>{
				uni.request({
					url: url,
					type: 'GET',
					success:(res)=> {
						_this.uploadQiniu(e,res.data.uploadToken)
					}
				})
			})
		},
		uploadQiniu(imgPath,token) {
			let _this = this
		    wx.uploadFile({
		      url: 'https://up.qiniup.com',
		      name: 'file',
		      filePath: imgPath,
		      header: {
		        "Content-Type": "multipart/form-data"
		      },
		     formData: {
		       token: token,
		     },
		     success: function(res) {
				 let data = JSON.parse(res.data)
				 let url = `https://qiniu.ishuber.com/${data.key}`
				_this.info.photo.push(url)
		     },
		     fail: function(res) {
			   let err = JSON.stringify(res)
			   uni.showToast({
			    title: err,
			   	icon: 'none',
			   	position: 'top',
			       duration: 5000
			   })
		     }
		   });
		},
		submit(){
			this.info.course = this.$store.state.user.userInfo.course
			let userInfo = this.info
			let url = `${this.$store.state.baseURL}/m/user_submit`
			let data = {
				url,
				userInfo
			}
			this.$store.dispatch('updateUser',data)
			.then((res)=>{
				uni.showToast({
				    title: res.data.msg,
					icon: 'none',
					position: 'bottom',
				    duration: 600
				});
				setTimeout(function(){
					console.log(url)
					uni.switchTab({
						url: `../../pagesB/my/my`
					})
				},600)
			})
		}
	}
}
